@import '~node_modules/modern-normalize/modern-normalize.css';

:root {
  --background-color: #f4f1ee;
  --second-color: #F8D24E;
  --white-color: #FCFCFC;
  --black-color: #000000;
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

ul {
  list-style: none;
}

body {
  font-family: "Open Sans", sans-serif;
  color: var(--black-color);
  font-weight: 400;
}
